
.card{
  width: 100%;
  margin: 30px auto 50px auto;

  display: flex;
  align-items: stretch;

  transition: opacity .1s ease-in-out;
}

@media screen and (max-width: 550px){
  .card{
    flex-direction: column;    
  }
}

.card figure{
  width: 100px;
  height: 100px;
  max-height: 100px;
  flex-shrink: 0;
  margin-right: 15px;
  overflow: hidden;
  border-radius: 50%;
  border: 1px solid #f9f9f9;

  background-color: #f9f9f9;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

@media screen and (max-width: 550px){
  .card figure{
    display: none;
  }
}

.card figure img{
  width: 100%;
}

.card .cardCategories{
  margin-top: 5px;
}

.card .cardCategories a{
  font-size: 11px;
  border-radius: 20px;
  display: inline-block;
  margin: 0 10px 3px 0;
}

.card .cardCategories a:hover{
  text-decoration: underline;
}

.card h1{
  font-weight: 700;
  font-size: 20px;
  margin-bottom: 10px;
}

.card .cardContent{
  flex-grow: 1;
}

.card .cardContent .cardFooter{
  border-top: 1px solid #eee;
  padding-top: 15px;
  margin-top: 15px;

  font-size: 13px;
}

@media screen and (max-width: 550px){
  .card .cardContent .cardFooter{
    border-bottom: 1px solid #eee;
    padding-bottom: 15px;
    border-top: none;
    padding-top: 0;
  }
}

.card .cardBody p{
  color: #666;
}

.card .cardFooter p a{
  color: #2878D2;
}

.card .cardFooter .linkAuthorName{
  color: inherit;
}

.card .cardFooter .linkAuthorName:hover,
.card .cardFooter .linkBlogName:hover{
  text-decoration: underline;
}


@keyframes placeHolderShimmer{
  0%{
      background-position: -468px 0
  }
  100%{
      background-position: 468px 0
  }
}


.card.empty figure,
.card.empty .cardTitle,
.card.empty .cardIntro,
.card.empty .cardAuthor,
.card.empty .cardDate,
.card.empty .cardCategories
{
  animation-duration: 1s;
  animation-fill-mode: forwards;
  animation-iteration-count: infinite;
  animation-name: placeHolderShimmer;
  animation-timing-function: ease-in;
  background: #f6f7f8;
  background: linear-gradient(to right, #eeeeee 8%, #e7e7e7 18%, #eeeeee 33%);
  background-size: 800px 104px;
}

.card.empty figure{ height: 125px; }
.card.empty .cardTitle,
.card.empty .cardIntro,
.card.empty .cardAuthor,
.card.empty .cardDate,
.card.empty .cardCategories
{
  height: 15px;
  color: transparent;
}


