.header{
  width: 100%;
  padding: 15px 15px 0 15px;

  border-bottom: 1px solid #ddd;

  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.logo{
  font-size: 28px;
  font-weight: 700;

  margin-bottom: 15px;
}

.tabs{
  width: 100%;

  display: flex;
  align-items: center;
  justify-content: center;
}

.tabs ul{
  display: inherit;
  align-items: inherit;
  justify-content: inherit;
}

.tabs li:not(:last-of-type){
  margin-right: 15px;
}

.tabs li a{
  text-decoration: none;
  padding: 10px;
  display: block;
  color: #333;
  border-bottom: 1px solid rgba(255, 255, 255, 0);
}

.tabs a.active{
  border-bottom: 1px solid #2878D2;
}

.tabs a.active{
  color: #2878D2;
}


